import React from "react"
import Layout from "../components/Layout"
import Posts from "../components/Posts"
import { Link, graphql } from "gatsby"

//export query so gatsby can grab it as a prop
export const markdownuery = graphql`
  query {
    allDirectory(filter: { absolutePath: { regex: "/posts//" } }) {
      edges {
        node {
          absolutePath
          id
          name
        }
      }
    }
  }
`

const categoryTemplate = props => {
  return (
    <div className="post">
      <Layout>
        <div className="title_wrap">
          <div className={"category_box " + props.pageContext.slug}>
            <div className="category_tit">{props.pageContext.slug}</div>
            <Posts category={props.pageContext.slug} />
          </div>
        </div>
      </Layout>
    </div>
  )
}

export default categoryTemplate
